import React from "react";
import ReactDOM from 'react-dom/client';
import saduicon from "../assets/images/icons/Saudi Arabia.svg"
import usaicon from "../assets/images/icons/USA.svg"
import Cookies from "js-cookie";


export default function  Nav({setLang , Lan}){

    return <>
        <div style={{cursor:"pointer"}} className= { Lan == "ar" ? "NavAr" : "Nav"} onClick={() => {
            var la = Cookies.get('language');
            if(la == "ar") Cookies.set('language', "en");
            else Cookies.set('language', "ar");
            setLang(la)
        }}>
            <p>{Lan == "ar" ? "English" : ""}</p>
            <img src={ Lan == "ar" ? usaicon :  saduicon} alt="Lang" />
        </div>
    </>
}
